//Import React & Hooks
import React, { useEffect } from "react";
import { useNavigate, Link } from "react-router-dom";

//Import CSS & styles
import "./userLoginStyles.css";

//Import components
import heroImageSource from "../../../assets/photos/chacho-hero.png";

//Import Redux
import { useDispatch, useSelector } from "react-redux";
import {
  loginUserAction,
  resetAllUsersErrorsAction,
} from "../../../redux/slices/users/usersSlices";

//Import Formik & Yup
import { useFormik } from "formik";
import * as Yup from "yup";
const formSchema = Yup.object({
  email: Yup.string().required("Por favor chacal escribi tu email"),
  password: Yup.string().required("Por favor chacal escribi tu contrasea"),
});

//----------------------------------------
//COMPONENT
//----------------------------------------

const UserLogin = () => {
  //Dispatch const creation
  const dispatch = useDispatch();

  //Navigate const creation
  const navigate = useNavigate();

  //Formik configuration
  const formik = useFormik({
    initialValues: {
      email: "",
      password: "",
    },
    onSubmit: (values) => {
      //Dispatch the action
      dispatch(loginUserAction(values));
    },
    validationSchema: formSchema,
  });

  //Select state from store
  const storeData = useSelector((store) => store.users);
  const { appError, serverError, userAuth } = storeData;

  useEffect(() => {
    if (userAuth) {
      navigate("/home");
    }
  }, [userAuth, navigate]);

  //Reset all errors and success messages once the component is render to avoid carry forward from other components
  useEffect(() => {
    dispatch(resetAllUsersErrorsAction());
  }, [dispatch]);

  return (
    <div className="container login-page-container">
      <div className="hero-text">
        <h2>Hola chacal,</h2>
        <h1>
          Bienvenido a{" "}
          <span className="hero-text-brand">
            El Rincn de <span>Chacho</span>
          </span>
        </h1>
        <h4>
          El punto de encuentro entre <span>amigos</span>, <span>ftbol</span> y{" "}
          <span>apuestas</span>
        </h4>

        <form onSubmit={formik.handleSubmit}>
          <div>
            <label>Email</label>
            <input
              value={formik.values.email}
              onChange={formik.handleChange("email")}
              onBlur={formik.handleBlur("email")}
              type="email"
              name="email"
              id="email"
            ></input>
          </div>
          <div className="message-error">
            {formik.touched.email && formik.errors.email}
          </div>
          <div>
            <label>Password</label>
            <input
              value={formik.values.password}
              onChange={formik.handleChange("password")}
              onBlur={formik.handleBlur("password")}
              type="password"
              name="password"
            ></input>
          </div>
          <div className="message-error">
            {formik.touched.password && formik.errors.password}
          </div>

          {/*  Text corresponding to appError */}
          {appError || serverError ? (
            <h5 className="message-error">{appError}</h5>
          ) : null}

          <button type="submit">Login</button>
        </form>
        <Link className="forgot-password-link" to="/forgot-password">
          Olvidaste tu contrasea?
        </Link>
      </div>
      <div className="hero-image-container">
        <img src={heroImageSource} alt="Hero" className="hero-image" />
      </div>
    </div>
  );
};

export default UserLogin;
